.info .container:after {
  z-index: 100;
  user-select: none;
  position: absolute;
  top: -40px;
  background: url('../images/side-eventinfo.svg');
  background-size: 100%;
  height: 500px;
  background-repeat: no-repeat;
}
@media (max-width: 767px) {
  .info .container:after {
    right: 10px;
    width: 20px;
  }
}
@media (min-width: 768px) {
  .info .container:after {
    right: -30px;
    width: 30px;
  }
}
.streaming {
  position: relative;
  background-image: url('../images/dartconf-streaming.jpg');
  background-size: cover;
  text-align: center;
}
@media (max-width: 767px) {
  .streaming {
    padding: 200px 0;
  }
}
@media (min-width: 768px) {
  .streaming {
    padding: 200px 0;
  }
}
.streaming:before,
.streaming:after {
  content: '';
  height: 200px;
  position: absolute;
  width: 100%;
  left: 0;
  user-select: none;
}
.streaming:before {
  background: linear-gradient(to top, rgba(35, 37, 54, 0) 0%, #232536 100%);
  top: 0;
}
.streaming:after {
  background: linear-gradient(to bottom, rgba(35, 37, 54, 0) 0%, #232536 100%);
  bottom: 0;
}
